import React, { useEffect } from "react";
import Sidebar from "./Sidebar.js";
import "./Assets/css/dashboard.css";
import { Typography } from "@material-ui/core";
import { Link } from "react-router-dom";
import { Doughnut, Line } from "react-chartjs-2";
import { useSelector, useDispatch } from "react-redux";
import { listAdminProducts } from '../actions/productActions'
import { listOrders } from '../actions/orderActions'
import { listUsers } from '../actions/userActions'
import MetaData from "../layout/MetaData";

function Dashboard({ history, match }) {
  const dispatch = useDispatch();

  const adminProducts = useSelector(state => state.adminProducts)
  const {  products } = adminProducts

  const orderList = useSelector(state => state.orderList)
  const {  orders } = orderList

  const userList = useSelector(state => state.userList)
  const {  users } = userList

  const userLogin = useSelector(state => state.userLogin)
  const { userInfo } = userLogin

  let outOfStock = 0;

  products &&
    products.forEach((item) => {
      if (item.Stock === 0) {
        outOfStock += 1;
      }
    });

  useEffect(() => {
    if (userInfo && userInfo.isAdmin) {
        dispatch(listOrders());
        dispatch(listUsers());
        dispatch(listAdminProducts());
    } else {
        history.push('/login')
    }

  }, [dispatch, history, userInfo])

  let totalAmount = 0;
  orders &&
    orders.forEach((item) => {
      if(item.isPaid){
        totalAmount += parseFloat(item.totalPrice);
      }
      
    });

  const lineState = {
    labels: ["Initial Amount", "Amount Earned"],
    datasets: [
      {
        label: "TOTAL AMOUNT",
        backgroundColor: ["tomato"],
        hoverBackgroundColor: ["rgb(197, 72, 49)"],
        data: [0, totalAmount],
      },
    ],
  };

  const doughnutState = {
    labels: ["Out of Stock", "InStock"],
    datasets: [
      {
        backgroundColor: ["#00A6B4", "#6800B4"],
        hoverBackgroundColor: ["#4B5000", "#35014F"],
        data: [outOfStock, products.length - outOfStock],
      },
    ],
  };

  return (
    <div className="dashboard">
      <MetaData title="Dashboard - Admin Panel" />
      <Sidebar />

      <div className="dashboardContainer">
        <Typography component="h1">Dashboard</Typography>
        <div className="dashboardSummary">
          <div>
            <p>
              Total Amount <br /> {totalAmount} NGN
            </p>
          </div>
          <div className="dashboardSummaryBox2">
            <Link to="/admin/productlist">
              <p>Product</p>
              <p>{products && products.length}</p>
            </Link>
            <Link to="/admin/orderlist">
              <p>Orders</p>
              <p>{orders && orders.length}</p>
            </Link>
            <Link to="/admin/userlist">
              <p>Users</p>
              <p>{users && users.length}</p>
            </Link>
          </div>
        </div>

        <div className="lineChart">
          <Line data={lineState} />
        </div>

        <div className="doughnutChart">
          <Doughnut data={doughnutState} />
        </div>
      </div>
    </div>
  );
};

export default Dashboard;
